// pages/perfil/page.tsx ou app/perfil/page.tsx (se a página ainda for "Server Component")

// Importe sua própria função de verificação de sessão
import { verificarSessao } from '@/lib/auth'; // Você precisará criar isso
import { redirect } from "next/navigation";

export default async function PerfilPage() {
  const sessaoUsuario = await verificarSessao(); // Sua função para pegar a sessão

  if (!sessaoUsuario || !sessaoUsuario.isLoggedIn) { // Adapte conforme sua função 'verificarSessao' retornar
    redirect("/login");
  }

  return (
    <div className="p-8">
      <h1 className="text-2xl font-bold">Perfil</h1>
      {/* Adapte conforme os dados do usuário que sua sessão contém */}
      <p>Bem-vindo, {sessaoUsuario.user?.nome || sessaoUsuario.user?.email}</p>
    </div>
  );
}